() {\n                return this.$store.getters.menu\n            },\n            tourModule() {\n                return this.$store.getters.tourModule\n            },\n            preloader() {\n                return this.$store.getters.preloader\n            },\n            steps() {\n                return this.$store.getters.steps\n            }\n        },\n        methods:\n        {\n            nextStep() {\n                this.$store.dispatch('changeStep2', true);\n                this.$router.push('/tour-module/tour')\n            },\n            chooseUkraine() {\n                    this.$store.dispatch('changeCountry', 'Украина')\n            },\n            chooseRussia() {\n                    this.$store.dispatch('changeCountry', 'Россия')\n            },\n            toggleChoice() {\n                if(this.tourModule.country=='Россия') {\n                    // this.tourModule.country = 'Украина'\n                    this.$store.dispatch('changeCountry', 'Украина')\n                }\n                else {\n                    // this.tourModule.country='Россия'\n                    this.$store.dispatch('changeCountry', 'Россия')\n                }\n            }\n        }\n    }\n</script>\n\n<style scoped>\n    .title {\n        font-family: 'Bello Pro';\n        color:#85b8e7;\n        cursor: pointer;\n    }\n    .active {\n        color:#f08b23 !important;\n    }\n    .country-form {\n        max-width: 500px;\n        margin: auto;\n        margin-top: auto;\n        /*text-align: center;*/\n    }\n    .section {\n        padding: 0 16px;\n    }\n    .title-1 {\n        position: absolute;\n        z-index: 1;\n        left: 0px;\n        top: 10px;\n        color:#062348;\n        font-family: 'Bello Pro' !important;\n        font-size: 34px;\n    }\n    .title-1 span {\n        font-size: 40px;\n    }\n    .title-2 {\n        position: absolute;\n        z-index: 1;\n        left: 48px;\n        top: 20px;\n        color:white;\n     