 }\n    .radio-title {\n        font-weight: 900;\n        font-family: \"Open Sans\";\n        text-transform: uppercase;\n        font-size: 20px;\n    }\n    .radio-active .ell {\n        width: 20px;\n        height: 20px;\n        background: #f08b23;\n        border: 2px solid #f08b23;\n        border-radius: 50%;\n    }\n    .radio-noactive .ell {\n        width: 20px;\n        height: 20px;\n        background: #fff;\n        border: 2px solid #f08b23;\n        border-radius: 50%;\n    }\n\n</style>\n"],"sourceRoot":""}]);
